@import './Variables.scss';

* {
    box-sizing: border-box;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6{
    margin:0;
}

table, th, td {
    border: 1px solid black;
  }


.editor {
    position: relative;
    max-width: 600px;
    height: auto;
    margin: 0 auto;
    border: 1px solid black;
    box-shadow: 0 0 10px black;
    margin-top: 20px;
    color: $main-text-color;

    &-title {
        padding: 5px;
        border-bottom: 1px solid black;

        &-text {
            font-weight: bold;
            display: inline-block;
            margin-left: 5px;
        }

        &-logo {
            display: inline;
        }

        &-expand {
            position: absolute;
            display: inline-block;
            right: 5px;
            cursor: pointer;
        }

        &-expand:hover {
            color: $lightColor;
        }
    }

    &-textArea {
        
        textarea {
            background-color: $backgroundColorLight;
            outline: none;
            resize: vertical;
            width: 100%;
            display: block;
            color: black;
        }
    }
}

#text-Area {
    height: 235px;
}

.preview {
    display: block;
    position: relative;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    max-width: 830px;
    height: auto;
    border: 1px solid black;
    box-shadow: 0 0 10px black;
    color: $main-text-color;

    &-title {
        padding: 5px;
        border-bottom: 1px solid black;

        &-text {
            font-weight: bold;
            display: inline-block;
            margin-left: 5px;
        }

        &-logo {
            display: inline;
        }

        &-expand {
            position: absolute;
            display: inline-block;
            right: 5px;
            cursor: pointer;
        }

        &-expand:hover {
            color: $lightColor;
        }
    }

    &-previewArea {
        overflow: hidden;
        background-color: $backgroundColorLight;
        img {
            display: block;
            max-width:100%;
            height:auto;
        }
    }
}

#previewAreaId {
    border: 1px solid;
    height: auto;
}